ulma-fluent/master/css/bulma.min.css\"\n                >https://github.com/mubaidr/bulma-fluent/tree/master/css</a>\n              </p>\n            </li>\n          </ol>\n        </div>\n\n        <div class=\"content\">\n          <h2 class=\"subtitle is-3\">\n            Customize\n          </h2>\n          <p>\n            Simply set your own Sass variables before importing Bulma-fluent.\n          </p>\n          <p>\n            <code lang=\"scss\">\n              // Set your brand colors <br>\n              $primary: #8A4D76 <br>\n              $info: #FA7C91 <br>\n              $success: #757763 <br>\n              $warning: yellow <br>\n              $danger: red <br>\n              $light: #ccc <br>\n              $dark: #444 <br>\n              <br>\n              // Update font family <br>\n              $family-sans-serif: \"Calibri\", \"Arial\" <br>\n              <br>\n              // Import the Bulma-fluent <br>\n              @import \"bulma-fluent/bulma.sass\" <br>\n            </code>\n          </p>\n        </div>\n\n        <div class=\"content\">\n          <h2 class=\"subtitle is-3\">\n            Documentation\n          </h2>\n          <p>\n            You can follow\n            <a\n              href=\"https://bulma.io/documentation/\"\n            >official bulma documentaion</a>\n            for all components.\n          </p>\n        </div>\n      </div>\n    </section>\n  </div>\n</template>\n\n<script>\nexport default {}\n</script>\n\n<style>\n.padded-list li {\n  padding: 1em 0;\n}\n</style>\n"]}]);
